/* links
	----------------------------------- */
.link-decor {
	color: #008CFF;
	span {
		padding-bottom: 3px;
		background-image: linear-gradient(to right, #008CFF 33%, rgba(255, 255, 255, 0) 0%), linear-gradient(to left, #008CFF 16%, rgba(255, 255, 255, 0) 0%);
		background-size: 22px 2px, 6px 2px;
		background-repeat: repeat-x;
		background-position: bottom; }
	&:hover span {
		color: #0051FF;
		background-image: linear-gradient(to right, darken(#008CFF, 10) 33%, rgba(255, 255, 255, 0) 0%), linear-gradient(to left, darken(#008CFF, 10) 16%, rgba(255, 255, 255, 0) 0%); }
	&_rubric {
		line-height: 1.35;
		span {
			background-image: linear-gradient(to right, #B3B3B3 33%, rgba(255, 255, 255, 0) 0%), linear-gradient(to left, #B3B3B3 16%, rgba(255, 255, 255, 0) 0%); }
		&:hover {
			border-bottom-color: transparent!important; }
		&:hover span {
			background-image: linear-gradient(to right, #0051FF 33%, rgba(255, 255, 255, 0) 0%), linear-gradient(to left, #0051FF 16%, rgba(255, 255, 255, 0) 0%); } } }

.link-sale {
	color: #FCBD7E;
	font-size: 20px;
	@include trs;
	span {
		border-bottom: 2px dashed #FCBD7E; }
	&:hover span {
		color: darken(#FCBD7E, 15);
		border-bottom-color: darken(#FCBD7E, 15); } }

/* about text
	----------------------------------- */
.about-text {
	@include md {
		margin-bottom: 10px; }
	&__container {
		max-width: 1500px;
		@include md {
			max-width: 1200px; }
		@include sm {
			max-width: 970px; } }
	&__title {
		text-align: center;
		margin-bottom: 20px;
		font-size: 23px;
		@include lg {
			font-size: 21px; }
		@include md {
			font-size: 18px;
			margin-bottom: 10px; }
		@include sm {
			font-size: 16px; } }
	&__descr {
		line-height: (30/22);
		text-indent: 45px;
		font-size: 20px;
		@include lg {
			font-size: 19px; }
		@include md {
			font-size: 16px; }
		@include sm {
			font-size: 14px; } } }

// pic decor
.decor {
	position: relative;
	background-color: #F4F8FD;
	&::after {
		position: absolute;
		z-index: 1;
		right: 0;
		bottom: 0;
		content: '';
		width: 0;
		height: 0;
		border-style: solid;
		border-width: 0 0 20px 20px;
		border-color: transparent transparent #fff transparent; }
	&__line {
		position: absolute;
		left: 0;
		bottom: -1px;
		width: 87%;
		height: 1px;
		background-color: rgba(#DAE5EC, .65);
		align-self: flex-end;
		&::before, &::after {
			position: absolute;
			z-index: 1;
			bottom: 0;
			content: '';
			transition: border-color .3s; }
		&::before {
			right: -16px;
			@include arr(15,20, rgba(#000, .25),br); }
		&::after {
			right: -39px;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 22px 23px 0 0;
			border-color: rgba(238, 244, 251, 0.9) transparent transparent transparent;
			@include sm {
				right: -39px; } }
		&_suggest {
			width: 95%; } }
	&_sm {
		background-color: #D7DCE0;
		&::after {
			display: none; }
		.decor__line {
			width: 85%; }
		.decor__line:after {
			right: -20px;
			border-width: 19px 19px 0 0;
			background-color: #ececec; }
		.decor__line:before {
			right: -1px; } }

	&_xs {
		&::after {
			border-width: 0 0 20px 19px;
			right: -2px; }
		.decor__line {
			width: 80%;
			bottom: 0; }
		.decor__line::after {
			right: -26px;
			border-width: 18px 18px 0 0; }
		.decor__line::before {
			right: -8px;
			border-width: 0 0 18px 13px; } } }


/* breadcrumbs
	----------------------------------- */
.breadcrumbs {
	font-size: 0;
	&__link {
		display: inline-block;
		vertical-align: middle;
		font-size: 20px;
		span {
			display: inline-block;
			vertical-align: middle; }
		&::after {
			display: inline-block;
			vertical-align: middle;
			margin: 0 3px;
			content: '/'; }
		&:last-child::after {
			display: none; }
		&:hover {
			color: $hover; }
		&.is-active {
			pointer-events: none; } }
	&__link:hover::after {
		color: #000; }
	&__icon {
		display: inline-block;
		vertical-align: middle;
		margin-right: 5px;
		fill: #D0CFCF; } }

/* page
	----------------------------------- */
.page {
	&_default {
 }		//padding: 35px 0 0
	&__bar {
		position: relative;
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-left: 15px;
		min-height: 46px;
		background: #EBECEC;
		&::after {
			content: '';
			position: absolute;
			bottom: -1px;
			left: 5px;
			right: 5px;
			height: 1px;
			background: #D1D1D1;
			z-index: 1; }
		&-title {
			padding: 8px 30px;
			margin-left: auto;
			background: #F3F3F3;
			border-radius: 50px 0 0 50px;
			min-width: 250px;
			font-size: 21px;
			text-align: center;
			color: #333333;
			@include md;

			&_has-icon {
				position: relative;
				padding: 8px 75px 8px 30px; }
			&_big-icon {
				padding: 8px 85px 8px 30px; }
			&-icon {
				position: absolute;
				top: 50%;
				transform: translateY(-50%);
				right: 10px;
				fill: #C7CBCF; }
			.icon-machine {
				top: 47%;
				font-size: 27px; }
			.icon-pictures {
				top: 48%;
				font-size: 26px; }
			.icon-souvenirs_page {
				font-size: 25px; }
			.icon-souvenirs {
				right: 16px;
				font-size: 25px; }
			.icon-books_page {
				right: 15px;
				font-size: 25px; }
			.icon-presents {
				font-size: 28px;
				right: 14px; }
			.icon-balloons_sm {
				right: 20px;
				fill: #F3F3F3;
				stroke: #000;
				font-size: 30px;
				stroke-width: 1px; }
			.icon-best-offers {
				stroke: #C7CBCF;
				fill: #C7CBCF;
				font-size: 30px; }
			.icon-cups {
				right: 15px;
				font-size: 28px; } } }

	&__in {
		display: flex;
		justify-content: space-between;
		margin: 30px 0 0; }
	&__side {
		width: 18.5%; }
	&__main {
		display: flex;
		flex-direction: column;
		width: 80%; } }

/* pagination
	----------------------------------- */
.pagination {
	padding: 7px 0;
	background: #F7F9FC;
	font-size: 0;
	&__list {
		font-size: 0;
		text-align: center; }
	&__list-item {
		display: inline-block;
		vertical-align: middle;
		margin: 0 5px;
		&_prev {
			margin-right: 10px;
			text-align: center;
			.pagination__list-link {
				padding: 0;
				line-height: 39px; } }
		&_dots {
			margin: 0 10px; } }
	&__list-item.is-active &__list-link {
			background: #FFEBAB; }
	&__list-link {
		display: inline-block;
		font-size: 19px;
		font-family: Arial, sans-serif;
		color: #667FA6;
		padding: 6px 12px;
		border-radius: 5px;
		height: 39px;
		min-width: 38px;
		&:hover {
			background: #EDEDED; }
		&:active {
			background: #C8D6E6; } }
	&__list-link:hover &__icon-arr {
		fill: lighten(#667FA6, 20); }
	&__icon-arr {
		display: inline-block;
		vertical-align: middle;
		fill: #EDEDED;
		font-size: 27px; }

	&_product {
		margin: 20px 0 45px; }

	&_watched {
		margin: 30px 0;
		background: none; }

	&_news {
		margin-top: 25px; } }

/* sub-menu
	----------------------------------- */
.category-menu {
	position: relative;
	&::after {
		content: '';
		position: absolute;
		bottom: -1px;
		left: 5px;
		right: 5px;
		height: 1px;
		background: #D1D1D1;
		z-index: 1; }
	&__title {
		margin-bottom: 40px;
		text-align: center;
		color: #274F7F; }
	&__list {
		display: flex; }
	&__item {
		flex: 1 0 auto; }
	&__link {
		display: block;
		padding: 15px 5px;
		font-size: 19px;
		background: #F7FAFF;
		text-align: center;
		@include trs;
		&:hover {
			background: #E5F0FC; } }

	&_default {
		margin-top: auto; }
	&_basket, &_order {
		max-width: 1400px;
		margin: 50px auto 0; } }

/* rating
	----------------------------------- */
.rating {
	&__text {
		margin-bottom: 3px;
		color: #1C487A; }
	&__in {
		display: flex;
		flex-direction: row-reverse;
		justify-content: center; }
	&__input {
		display: none; }
	&__input ~ &__icon .rating__star {
		fill: #DBDBDB;
		stroke: #DBDBDB; }
	&__input:checked ~ &-icon .rating__star,
	&__input:checked &-icon .rating__star {
		fill: #FFED00;
		stroke: #FFED00; }
	&__icon:hover .rating__star,
	&__icon:hover ~ &__icon .rating__star {
		fill: #C7CBCF;
		stroke: #DBDBDB; }
	&__input:checked &__icon:hover .rating__star,
	&__input:checked ~ &__icon .rating__star {
		fill: #FFED00;
		stroke: #FFED00; }
	&__icon {
		display: inline-block;
		cursor: pointer; }
	&__star {
		font-size: 23px; } }

/* check
	----------------------------------- */
.check {
	&__circle {
		display: inline-block;
		margin-left: 4px;
		width: 24px;
		height: 24px;
		border: 2px solid #99AFCF;
		border-radius: 50%;
		text-align: center;
		line-height: 20px;
		&::after {
			display: inline-block;
			width: 14px;
			height: 14px;
			border-radius: 50%;
			background: #768FAE;
			content: '';
			transform: scale(.4);
			opacity: 0;
			transition: .15s; } }
	label {
		font-size: 19px;
		color: #000;
		cursor: pointer; }
	input {
		display: none; }
	input:checked + label .check__circle::after {
		transform: scale(1);
		opacity: 1; } }

/* label-block
	----------------------------------- */
.label-block {
	position: relative;
	&__title {
		position: absolute;
		top: 4px;
		left: 50%;
		transform: translateX(-50%);
		z-index: 1;
		padding: 10px;
		@include f('tcm', 16, 18);
		color: #004D8C;
		background: rgba(#DEF1FF, .6);
		text-transform: uppercase; }
	&__in {
		padding: 4px;
		border: 2px solid #47C1F0;
		@include clr; }
	&__item {
		position: relative;
		float: left;
		width: calc(50% - 5px);
		padding-top: 36%;
		margin-right: 5px;
		margin-bottom: 5px;
		@include clr;
		&:nth-child(4) {
			padding-top: 0; }
		&:nth-child(even) {
			width: 50%;
			margin-right: 0; }
		&-img {
			@include coverdiv;
			@include cover; }
		&_half {
			padding-top: 72%;
			margin-bottom: 0; }
		&_double {
			width: 50%;
			padding-top: 35%;
			margin-right: 0;
			&:nth-child(3) {
				margin-bottom: 0; } }
		&-label {
			position: absolute;
			left: 0;
			bottom: 0;
			padding: 10px;
			z-index: 1;
			@include f('oss', 16, 16);
			color: #fff;
			background: rgba(#000, .45);
			&_top {
				top: 0;
				bottom: auto; } } }

	&__logo {
		padding: 7px 0;
		background: #47C1F0;
		&-img {
			width: 86%;
			margin: 0 auto; } } }

/* benefits-list
	----------------------------------- */
.benefits-list {
	&__title {
		font-size: 20px;
		text-align: center;
		font-weight: 700; }
	&__descr {
		margin-top: 35px;
		text-indent: 30px; }
	ul {
		margin-top: 20px;
		li {
			text-indent: 30px; }
		li:not(:last-child) {
			margin-bottom: 20px; } }
	&__link {
		color: #274F7F;
		background: transparent;
		text-decoration: underline;
		text-decoration-color: #274F7F; }
	&_default {
		margin: 30px 0 0; } }

/* section defaults
	----------------------------------- */
.sect {
	padding-bottom: 40px; }

.sect-title {}

.sect-descr {
	margin: 20px 0;
	text-indent: 30px; }

/* zoom
	----------------------------------- */
.zoom {
	position: relative;
	&__element {
		display: block; }
	&__element:hover &__btn {
		opacity: 1; }
	&__img {
		width: 100%; }
	&__btn {
		position: absolute;
		left: -10px;
		bottom: -10px;
		padding: 10px;
		@include tr(opacity .3s);
		opacity: 0;
		&:hover &-icon {
			fill: darken(#B9BDC7, 10); }
		&:active &-icon {
			fill: darken(#B9BDC7, 20); }
		&-icon {
			font-size: 36px;
			fill: #B9BDC7; }
		&_sm {
			padding: 0;
			left: 6px;
			bottom: 4px; }
		&_sm &-icon {
			font-size: 28px;
			fill: #fff; }
		&_top {
			top: 8px;
			bottom: auto; }
		&_color &-icon {
			fill: #5b5f69; } } }

