body {
	min-width: 768px;
	@include f("psr", 18, 26);
	color: $black;
	-webkit-font-smoothing: subpixel-antialiased; }

// layout
.out {
	overflow: hidden; }

.container {
	max-width: 1560px;
	margin: 0 auto;
	padding: 0 15px;
	@include sm {
		padding: 0 10px; } }

// titles
h1, h2, h3, h4, h5, h6 {
	font-weight: normal; }

.title-xs {
	@include f("oss",19,30);
	color: $black; }

.title-sm {
	@include f("psb",20); }

.title-md {
	padding: 0 30px;
	@include f('psr', 21);
	line-height: 1.25;
	color: #00306B;
	text-transform: uppercase;
	&__color {
		color: #E63833; } }

.title-lg {
	@include f('psr',27);
	color: #00306B;
	text-transform: uppercase; }

/* buttons
	----------------------------------- */
.btn {
	position: relative;
	display: inline-block;
	min-width: 340px;
	padding: 16px 70px 16px 5px;
	@include f("psr",22);
	color: $white;
	outline: none;
	text-decoration: none;
	text-align: center;
	background-color: #52C0E9;
	border-bottom: 1px solid #E7E7E7;
	@include lg {
		min-width: 280px;
		font-size: 20px;
		padding: 13px 55px 13px 5px; }
	@include md {
		min-width: 238px;
		padding: 11px 41px 11px 5px;
		font-size: 17px; }
	@include sm {
		min-width: 210px;
		padding: 6px 34px 6px 5px;
		font-size: 15px; }
	&::after {
		content: '';
		position: absolute;
		top: 50%;
		transform: translateY(-50%);
		right: 32px;
		z-index: 1;
		width: 32px;
		height: 32px;
		@include ico('btn_arr');
		@include lg {
			right: 13px; }
		@include md {
			width: 26px;
			right: 10px; }
		@include sm {
			width: 21px;
			height: 20px; } }
	&:hover {
		background-color: #128FCD;
		border-bottom-color: #F9BF84; }
	&:hover::after {
		@include ico('btn_arr-hvr'); }
	&:active {
		background-color: #24568D;
		border-bottom-color: #E63833; }
	&:active::after {
		@include ico('btn_arr-act'); }
	&_dark {
		background-color: #1768BF;
		border-bottom-color: #F9BF84;
		&::after {
			@include ico('btn_arr_dark'); }
		&:hover {
			background-color: #00BBFF;
			border-bottom-color: #F9BF84; }
		&:hover::after {
			@include ico('btn_arr_dark-hvr'); }
		&:active {
			background-color: #24568D;
			border-bottom-color: #E63833; }
		&:active::after {
			@include ico('btn_arr-act'); } } }

.send-btn {
	position: relative;
	padding: 0 70px 0 5px;
	min-width: 232px;
	line-height: 45px;
	height: 45px;
	color: #fff;
	background: #6ca5db;
	text-align: right;
	border-bottom: 2px solid #a7b8c9;
	@include f("ver",19);
	overflow: hidden;
	&::after {
		position: absolute;
		top: 0;
		right: 0;
		width: 43px;
		height: 43px;
		@include ico('send');
		content: '';
		@include md {
			height: 36px;
			width: 36px; } }
	&:hover {
		background-color: #63afed;
		border-bottom-color: #ffff74; }
	&:hover::after {
		@include ico('send-hvr'); }
	&:active {
		background-color: #415c89;
		border-bottom-color: #a95a63; }
	&:active::after {
		@include ico('send-act'); } }

.btn-cart {
	position: relative;
	padding: 17px 90px 17px 50px;
	font-size: 21px;
	color: #fff;
	background: #2AB1E2;
	border-bottom: 2px solid #E3E3E3;
	overflow: hidden;
	&::after {
		position: absolute;
		top: -1px;
		right: -1px;
		width: 64px;
		height: 63px;
		@include ico('cart_add-lg');
		content: ''; }
	&:hover {
		background: #01B6F8;
		border-bottom-color: #FFBE77; }
	&:hover::after {
		@include ico('cart_add-lg-hvr'); }
	&:active {
		background: #23568A;
		border-bottom-color: #E63E2D; }
	&:active::after {
		@include ico('cart_add-lg-act'); } }

