.order-btn {
	position: relative;
	min-width: 380px;
	height: 75px;
	line-height: 75px;
	padding: 0 76px 0 10px;
	text-align: center;
	font-size: 25px;
	color: #fff;
	background: #28B0E2;
	border-bottom: 2px solid #E0E0E1;
	overflow: hidden;
	&::after {
		content: '';
		position: absolute;
		top: 0;
		right: -1px;
		@include sr('like'); }
	&:hover {
		background: #06B2F0;
		border-bottom-color: #FFBE77; }
	&:active {
		background: #23568B;
		border-bottom-color: #FF0000; }
	&:hover::after {
		@include sr('like-hvr'); }
	&:active::after {
		@include sr('like-act'); } }

.submit-btn {
	position: relative;
	min-width: 380px;
	height: 75px;
	line-height: 75px;
	padding: 0 76px 0 10px;
	text-align: center;
	font-size: 25px;
	color: #fff;
	background: #28B0E2;
	border-bottom: 2px solid #E0E0E1;
	overflow: hidden;
	text-transform: uppercase;
	span {
		border-radius: 2px;
		border-bottom: 2px solid #2B99CC; }
	&::after {
		content: '';
		position: absolute;
		top: 0;
		right: -1px;
		@include sr('subm'); }
	&:hover {
		background: #06B2F0;
		border-bottom-color: #FFBE77;
		span {
			border-bottom-color: #079DE3; } }
	&:active {
		background: #23568B;
		border-bottom-color: #FF0000;
		span {
			border-bottom-color: #254478; } }
	&:hover::after {
		@include sr('subm-hvr'); }
	&:active::after {
		@include sr('subm-act'); } }

.more-content-open {
	font-size: 18px;
	color: #004D87;
	span {
		display: inline-block;
		vertical-align: middle; }
	&__icon {
		display: inline-block;
		vertical-align: -1px;
		margin-left: 4px;
		font-size: 8px;
		fill: #004D87; } }
.more-content-close {
	display: none;
	padding-top: 9px;
	border-top: 1px solid #DFDFE0;
	width: 216px;
	&__icon {
		font-size: 16px;
		fill: #C2C2C2;
		@include rotate(180); } }

// basic transition
@mixin trs {
	transition: .2s cubic-bezier(0.55, 0.09, 0.68, 0.53); }

// mini reset
a {
	text-decoration: none;
	color: inherit; }

img {
	display: block; }

button {
	background: none;
	@include f('psr'); }

